.App {
	display: grid;
	margin: 0px;
	grid-template-rows: auto;
	position: relative;
}

.top {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	justify-items: center;
	align-items: center;
	background-color: var(--main-color);
	z-index: 100;
	box-shadow: 0px 10px 0px 0px var(--accent-color);
}